import {AfterViewInit, ChangeDetectorRef, Component, OnDestroy, OnInit, ViewEncapsulation} from "@angular/core";
import {ServerService} from "../../services/server-service";
import customerColumns from "../../models/customer";
import {GridOptions} from "ag-grid";
import {NavBarService} from "../../services/nav-bar.service";
import {ActivatedRoute, Router} from "@angular/router";
import {Subscription} from "rxjs/Subscription";


@Component({
  selector: 'app-parties',
  templateUrl: './parties.component.html',
  styleUrls: ['./parties.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PartiesComponent implements OnInit, AfterViewInit, OnDestroy {
  customerColumns = customerColumns;
  gridOptions: GridOptions;
  rowData: any[];
  navBarSubscription:Subscription;
  googleMapsClient:any;
  constructor(private serverService: ServerService,
              private navBarService: NavBarService,
              private changeDetectorRef: ChangeDetectorRef,
              private router:Router,
              private activatedRoute:ActivatedRoute) {

  }

  ngOnInit() {
    this.serverService.getAllParties()
      .subscribe((response: any) => {
        this.rowData = response._embedded.parties;
        console.log(this.rowData);
      }, (error) => {
        console.log(error);
      });
    this.gridOptions = {
      context: {
        componentParent: this
      },
      rowData: this.rowData,
      columnDefs: this.columnDefs
    };
    this.navBarSubscription = this.navBarService.getNavBarSubject().subscribe((value) => {
      // the timings of the nav bar css transition should be considered
      console.log("sizeColumnsToFit");
      // setTimeout(()=> {
      //   this.gridOptions.api.sizeColumnsToFit();
      // }, 500);
      this.gridOptions.api.sizeColumnsToFit();
    });
  }

  newCustomer() {

  }

  columnDefs = [
    {
      headerName: "Party Name",
      field: 'name',
      cellClass: 'first-column'
    },
    {
      headerName: "Address",
      field: "physical_address",
      width: 400,
      cellClass: 'second-column'
    },
    {
      headerName: "Active Jobs Count",
      field: "active_job_count",
      cellClass: 'third-column'
    }
  ];

  onModelUpdated() {
    console.log("onModeUpdated");
  }

  onCellClicked($event) {
    console.log($event.data);
    let href = $event.data._links.self.href;
    let index = (href.lastIndexOf('/') + 1);
    let id  = +(href.substr(index));
    console.log(id);
    this.router.navigate(['party', id])
        .then((value)=>{
        console.log(value);
    }).catch((error)=>{console.log(error)});
  }

  onCellDoubleClicked($event) {
    console.log($event.data._links.self);
  }

  ngAfterViewInit() {
    this.gridOptions.api.sizeColumnsToFit();
  }

  ngOnDestroy() {
    this.navBarSubscription.unsubscribe();
  }
}



