import {AfterViewInit, Component, OnInit, Query, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, Params, Router} from "@angular/router";
import {ServerService} from "../../../services/server-service";
import {Location} from "@angular/common";

@Component({
  selector: 'app-party',
  templateUrl: './party.component.html',
  styleUrls: ['./party.component.scss'],
  encapsulation:ViewEncapsulation.None
})
export class PartyComponent implements OnInit, AfterViewInit {
  partyBills:any= null;
  partyInvoices:any = null;

  constructor(private activatedRoute: ActivatedRoute,
              private router: Router,
              private serverService: ServerService,
              private location: Location) {}

  data:any;

  ngOnInit() {

  }
  ngAfterViewInit() {
    this.activatedRoute.params.subscribe((params) => {
      console.log(params['id']);
      this.serverService.getRequestByPartyId(params['id'], null, {params:'projection=partyProjection'})
        .subscribe((data: any) => {
          this.data = data;
          console.log(data);
        }, (error) => {
          console.log(error);
        });
      this.serverService.getRequestByPartyId(params['id'], '/bills')
        .subscribe((bills: any) => {
          this.partyBills = bills._embedded.bills;
          console.log(bills);
        }, (error) => {
          console.log(error);
        });
      this.serverService.getRequestByPartyId(params['id'], '/invoices')
        .subscribe((invoices: any) => {
          this.partyInvoices = invoices;
          console.log(invoices);
        }, (error) => {
          console.log(error);
        });
    }, (error) => console.log(error));

  }

}



