import {Injectable} from '@angular/core';
import {Http, RequestOptionsArgs, Response, Headers} from '@angular/http';
import 'rxjs/Rx';

import {Party} from "../models/customer";
import {Observable} from "rxjs/Observable";
import {PartyModel} from "../models/party.model";


@Injectable()
export class ServerService {
  private baseUrl = "http://localhost:8080";
  private apiUrl = this.baseUrl + "/api";
  private partiesUrl = this.apiUrl + "/parties";
  constructor(private http: Http) {
  }

  optionsArgument: RequestOptionsArgs = "page=1";

  saveCustomer(customer: Party) {
    console.log(customer);
    return this.http.post('http://localhost:8080/setParty', customer);
  }

  getAllParties() {
    return this.http.get(this.partiesUrl)
      .map(
        (response: Response) => {
          const data = response.json();
          console.log(data);
          return data;
        }).catch(
        (error: Response) => {
          return Observable.throw('Something went wrong');
        }
      );
  }

  getRequestByPartyId(id: number, path?: string, reuquestOptionParams?: RequestOptionsArgs) {
    console.log(path);
    path = path ? path : '';
    return this.http.get(this.partiesUrl + "/" + id + path, reuquestOptionParams)
      .map(
        (response: Response) => {
          const data = response.json();
          console.log(data);
          return data;
        }).catch(
        (error: Response) => {
          return Observable.throw('Something went wrong');
        }
      );
  }

  //* var firstHeaders = new Headers();
  //* firstHeaders.append('Content-Type', 'image/jpeg');
  saveParty(party: PartyModel) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post("http://localhost:8080/api/parties/", party, {headers: headers});
  }

  patchParty(id, party: PartyModel) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.patch("http://localhost:8080/api/parties/" + id  , party, {headers: headers});
  }

  putParty(id, party: PartyModel) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.put("http://localhost:8080/api/parties/" + id , party, {headers: headers});
  }
}



