import { Injectable } from '@angular/core';
import {Subject} from "rxjs/Subject";

@Injectable()
export class NavBarService {
  navBarSubject:Subject<any> = new Subject();
  constructor() { }

  getNavBarSubject() {
    return this.navBarSubject;
  }

}



